import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from '../@shared/layout/layout.component'
import { DashboardComponent } from './dashboard/dashboard.component';
import { UsersComponent } from './users/users.component';
import { SlotLayoutComponent } from './slot-layout/slot-layout.component';
import { ParkingLotListComponent } from './parking-slot/parking-lot-list/parking-lot-list.component';
import { ParkingLotAddUpdateComponent } from './parking-slot/parking-lot-add-update/parking-lot-add-update.component';

const routes: Routes = [
  {
    path: '',
    component: LayoutComponent,
    children: [
      {
        path: 'dashboard',
        component: DashboardComponent
      },
      {
        path: 'parking-slot/layout',
        component: SlotLayoutComponent
      },
      {
        path: 'parking-slot/list',
        component: ParkingLotListComponent
      },
      {
        path: 'parking-slot/add',
        component: ParkingLotAddUpdateComponent
      },
      {
        path: 'users',
        component: UsersComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule { }
