import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-parking-lot-add-update',
  templateUrl: './parking-lot-add-update.component.html',
  styleUrls: ['./parking-lot-add-update.component.scss']
})
export class ParkingLotAddUpdateComponent implements OnInit {
  formGroup: FormGroup;
  isSubmit = false;
  numbers = [];
  locations = [
    { label: "Wipro Gate 1", value: "wipro_gate_1" },
    { label: "Wipro Gate 2", value: "wipro_gate_2" },
    { label: "Wipro Gate 3", value: "wipro_gate_3" },
    { label: "Wipro Gate 4", value: "wipro_gate_4" }
  ];
  constructor(protected fb: FormBuilder) { }

  ngOnInit() {
    this.numbers = Array.from(Array(20), (x, i) => i + 1);
    this.initForm();
  }

  initForm() {
    this.formGroup = this.fb.group({
      parkingId: ['', Validators.required],
      area: ['', Validators.required],
      location: ['', Validators.required],
      lat: ['', Validators.required],
      lng: ['', Validators.required],
      noOfSlots: ['', Validators.required],
      type: ['', Validators.required],
      cameraId: ['', Validators.required],
    })
  }

  get f() {
    return this.formGroup.controls;
  }

  submit(e) {
    this.isSubmit = true;
  }
}
