import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { WebserviceService } from '../services/webservice.service'
import { api } from '../services/api'
import { environment } from 'src/environments/environment';
import { BaseClass } from 'src/app/@shared/base-class/base-class';
import { MessageService } from '../../@shared/message/message.service'
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { DataSource } from '@angular/cdk/table';
import { IbaseService } from 'src/app/@shared/base-service/ibase-service';
import {MatBottomSheet, MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA} from '@angular/material/bottom-sheet';
import { Subject } from 'rxjs';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {    
    mapMarker = [
        {
            lat: 23.0225,
            lng: 72.5714,
            label: "P1",
            draggable: false
        },
        {
            lat: 21.170240,
            lng: 72.831062,
            label: "P2",
            draggable: false
        }
    ];

    public api = api
    public env = environment
    public userData = JSON.parse(localStorage.getItem('loginDetail'))

    displayedColumns: string[] = ['uName', 'pName', 'uEmail', 'uPhone', 'uStatus', 'action'];
    dataSource = new MatTableDataSource([]);
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
    // service: any;

    constructor(private _bottomSheet: MatBottomSheet, protected _message: MessageService, protected service: WebserviceService) {
        // super()
    }

    openBottomSheet(): void {
        // this._bottomSheet.open(SlotLayoutSheet);
        this._bottomSheet.open(SlotLayoutSheet, {
            data: { names: ['Frodo', 'Bilbo'] },
          });
      }

    ngOnInit() {
        // this.getUsers(5);
    }

    clickedMarker(label: string, index: number) {
        console.log(`clicked the marker: ${label || index}`)
    }

    onMouseOver(infoWindow, $event: MouseEvent) {
        infoWindow.open();
    }

    onMouseOut(infoWindow, $event: MouseEvent) {
        infoWindow.close();
    }

    getServices(): IbaseService<any> {
        return this.service
    }

    applyFilter(filterValue: string) {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }

    getUsers(roleId) {
        let req = {
            id: 0,
            role: roleId == 1 ? 2 : roleId == 2 ? 4 : 5
        }
        this.getServices().Post(`${this.env.baseUrl + this.api.users}`, req).subscribe(res => {
            this.setData(res.data)
            this._message.succmessage(res.message)
        }, err => {
            console.log(err);
            this._message.errmessage(err)
        })
    }

    setData(data) {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.sort = this.sort;
        this.dataSource.paginator = this.paginator;
    }

    changeStatus(e, event, i) {
        if (confirm('Are you sure!')) {
            let req = {
                userId: event.uId,
                roleId: event.uRole,
                status: e.checked,
            }
            this.getServices().Post(`${this.env.baseUrl + this.api.updateStatus}`, req).subscribe(res => {
                this._message.succmessage(res.message)
            }, err => {
                this._message.errmessage(err)
            })
        } else {
            this.dataSource.filteredData[i].uStatus = e.checked ? 0 : 1
        }
    }
}


