import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { FormControl, FormGroup, FormArray, FormBuilder, Validators, AbstractControl } from '@angular/forms'
import { WebserviceService } from '../services/webservice.service'
import { api } from '../services/api'
import { environment } from 'src/environments/environment';
import { BaseClass } from 'src/app/@shared/base-class/base-class';
import { MessageService } from '../../@shared/message/message.service'
import { MatTableDataSource, MatSort, MatPaginator, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material'
import { DataSource } from '@angular/cdk/table';
import { IbaseService } from 'src/app/@shared/base-service/ibase-service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent extends BaseClass<any> implements OnInit {

  public api = api
  public env = environment
  public userData = JSON.parse(localStorage.getItem('loginDetail'))

  displayedColumns: string[] = ['username', 'role', 'status', 'action'];
  dataSource = new MatTableDataSource([]);
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild('callDialog') callDialog: TemplateRef<any>
  dialogRef;
  pId = []; // Parent userid
  pRole = []; // Parent role
  formGroup: FormGroup
  data = [];

  constructor(protected service: WebserviceService, protected dialog: MatDialog, protected fb: FormBuilder, protected _message: MessageService, protected webservice: WebserviceService) {
    super()
  }

  ngOnInit() {
    // this.getUsers(this.userData.id, this.userData.role, true);
    this.initForm();
    this.getUsers();
  }

  initForm() {
    this.formGroup = this.fb.group({
      childId: [0, Validators.required],
      fullName: ['', Validators.required],
      username: ['', Validators.required],
      // password: ['', [Validators.pattern("^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,10}$")]],
      password: ['',],
      confPass: ['',],
      phoneNo: ['', [Validators.pattern(/^-?(0|[1-9]\d*)?$/)]],
      emailAdd: ['', [Validators.email]],
      address: ['',],
      masterPass: ['', Validators.required],
      action: ['insert', Validators.required]
    }, {
        validator: [this.MatchPassword, this.setValidation]
      })
  }

  setValidation(control: AbstractControl){
    let action = control.get('action').value
    if(action == 'insert'){
      control.get('password').setErrors({ required: true })
      control.get('confPass').setErrors({ required: true })
    } else {
      control.get('password').setErrors(null)
      control.get('confPass').setErrors(null)
    }
  }

  MatchPassword(control: AbstractControl) {
    let password = control.get('password').value;
    let confPass = control.get('confPass').value;
    if (password != confPass) {
      control.get('confPass').setErrors({ ConfirmPassword: true });
    } else {
      return null;
    }
  }

  getServices(): IbaseService<any> {
    return this.service
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // getUsers(uId, roleId, status) {
  //   this.setParentData(uId, roleId, status);
  //   this.userList(uId, roleId, status)
  // }

  userList(uId, roleId, status) {
    let req = {
      id: uId,
      role: roleId == 1 ? 2 : roleId == 2 ? 4 : 5
    }
    this.webservice.Post(`${this.env.baseUrl + this.api.users}`, req).subscribe(res => {
      this.data = res.data
      this.setData()
    }, err => {
      this._message.errmessage(err)
    })
  }

  getUsers(){
    this.webservice.Get(`${this.env.baseUrl + this.api.users}`).subscribe(res => {
      this.data = res.data
      this.setData()
    }, err => {
      this._message.errmessage(err)
    })
  }

  setParentData(uId, roleId, status) {
    if (status) {
      this.pId.push(uId)
      this.pRole.push(roleId)
    } else {
      this.pId.pop()
      this.pRole.pop()
    }
  }

  backToParent() {
    let id = this.pId[this.pId.length - 2]
    let role = this.pRole[this.pRole.length - 2]
    if (id && role) {
      // this.getUsers(id, role, false)
    }
  }

  stayOnChild() {
    let id = this.pId[this.pId.length - 1]
    let role = this.pRole[this.pRole.length - 1]
    if (id && role) {
      this.userList(id, role, false)
    }
  }

  setData() {
    this.dataSource = new MatTableDataSource(this.data);
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  delete(element) {
    if (confirm('Are you sure!')) {
      let para = {
        childId: element.uId
      }
      this.getServices().Post(`${this.env.baseUrl + this.api.deleteUser}`, para).subscribe(res => {
        this._message.succmessage(res.message)
        this.stayOnChild()
      }, err => {
        this._message.errmessage(err)
      })
    }
  }

  openDialog(e, status) {
    let data = {
      childId: e.uId ? e.uId : 0,
      fullName: e.pName ? e.pName : '',
      username: e.uName ? e.uName : '',
      password: '',
      confPass: '',
      phoneNo: e.pPhone ? e.pPhone : '',
      emailAdd: e.pEmail ? e.pEmail : '',
      address: e.pAdd ? e.pAdd : '',
      masterPass: '',
      action: status
    }
    this.isSubmit = false
    this.formGroup.setValue(data);
    this.dialogRef = this.dialog.open(this.callDialog), {
      height: '400px',
      width: '600px',
    };
    this.dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  closeDialog() {
    this.dialogRef.close('callDialog');
  }

  get f() {
    return this.formGroup.controls;
  }

  submit(formGroup) {
    console.log(this.f);    
    this.isSubmit = true
    if (formGroup.invalid) {
      return
    }
    this.getServices().Post(`${this.env.baseUrl + this.api.upsertusers}`, formGroup.value).subscribe(res => {
      this._message.succmessage(res.message)
      this.stayOnChild()
      setTimeout(() => {
        this.closeDialog()
      }, 1000)
    }, err => {
      this._message.errmessage(err)
    })
  }

  changeStatus(e, event, id) {
    if (confirm('Are you sure!')) {
      let req = {
        userId: event.uId,
        roleId: event.uRole,
        status: e.checked,
      }
      this.getServices().Post(`${this.env.baseUrl + this.api.updateStatus}`, req).subscribe(res => {
        this._message.succmessage(res.message)
        this.stayOnChild()
      }, err => {
        this._message.errmessage(err)
      })
    } else {
      this.setData();
      event.uStatus = !e.checked;
    }
  }

}
